.Post{
    background-color: #fff;
    width: 100%;
    border-radius: 16px;
    margin-bottom: 1.5vh;
}
.PostAuthor{
    width: 100%;
    padding-left: 3vh;
    padding-top: 3vh;
    display: flex;
}
.PostAuthorPFP{
    height: 5vh;
}
.PostAuthorInfo{
    width: 90%;
    height: 5vh;
    margin-left: 1vh;
}
.PostAuthorName{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 2vh;
    line-height: 17px;
    margin-top: 0.5vh;
}
.PostTime{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 1.5vh;
    line-height: 12px;
    margin-top: 0.5vh;
}
.PostContent{
    width: 100%;
    padding-left: 3vh;
    margin-top: 1vh;
}
.PostPhoto{
    border-radius: 17px;
    max-width: 90%;
    max-height: 50vh;
}
.PostPhotoParent{
    border-radius: 17px;
    margin-top: 1.5vh;
}
.Like{
    display: flex;
    border-radius: 2.5vh;
    background-color: #EEEFF8;
    width: 2vw;
    height: 2vh;
    padding: 0.5vh;
}
.LikeImg{
    height: 70%;
    margin-left: 0.5vh;
}
.LikeCount{
    margin-left: 0.2vw;
}
.feedbackContainer{
    display: flex;
    margin-left: 1.5vh;
}