.Header{
    height: 10vh;
    padding-left: 15vw;
    display: flex;
    background-color: #FFFFFF;
}
.Logo{
    min-height: 10vh;
    width: 15vw;
    position: relative;
}
.App-logo {
    height: 5vh;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    margin-left: 10%;
}
.Search{
    width: 40vw;
    position: relative;
}
.Search-input{
    border: 1px solid #CBC0FF;
    border-radius: 7px;
    height: 5vh;
    width: 20vw;
    border-color: #CBC0FF;
    background: #FAFBFF;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}
.Search-button{
    height: 5vh;
    width: 7vw;
    margin-left: 21vw;
    background: #7040FF;
    border-radius: 7px;
    border: none;
    color: #FFFFFF;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}
.reference{
    width: 15vw;
}